<template>
  <div>
    <Featured/>
    <Topics/>
    <Speakers/>
    <WhyAttend/>
    <Supporters/>
  </div>
</template>

<script>
  export default {
    name: 'Home',

    components: {
      Featured: () => import('./Featured'),
      Topics: () => import('./Topics'),
      Speakers: () => import('./Speakers'),
      WhyAttend: () => import('./WhyAttend'),
      Supporters: () => import('./Supporters'),
    },
  }
</script>

<style lang="scss" scoped>
  canvas {
    display: block;
    vertical-align: bottom;
  }

  .link-more {
    // background: white url('../images/speakers/molecule.png') no-repeat;
    background-size: cover;

    a {
      text-decoration: none !important;
      color: #3f3f3f !important;
    }
  }

  /* ---- particles.js container ---- */

  #particles-js {
    z-index: -9999;
    position: fixed;
    width: 100%;
    height: 100%; // background-color: $purple;
    //background-image: url('');
    background-repeat: no-repeat;
    background-size: 20%;
    background-position: 50% 50%;
  }

  /* ---- END particles.js container ---- */
</style>



